import axios from 'axios'
import React, { useEffect, useRef, useState } from 'react'
import { Button, Card, Col, Container, Form, Modal, Row, Table } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { message } from 'antd'
import { CBadge } from '@coreui/react'
import ReactPaginate from 'react-paginate'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [pageCount, setPageCount] = useState(1)
  const limit = 8
  const currentPage = useRef(1)
  const [validated, setValidated] = useState(false)
  const [firstName, setFirstname] = useState()
  const [lastName, setLastname] = useState()
  const [mobile, setMobile] = useState()
  const [email, setEmail] = useState()
  const [complaintMessage, setComplaintMessage] = useState()
  const [complaintTitle, setComplaintTitle] = useState()
  const [complaintImage, setComplaintImage] = useState()
  const [complaintsHistory, setComplaintsHistory] = useState([])

  const [show, setShow] = useState(false)

  const [modalTitle, setModalTitle] = useState('')
  const [modalMessage, setModalMessage] = useState('')
  const [modalScreenshot, setModalScreenshot] = useState('')
  const [modalComplaintId, setModalComplaintId] = useState('')

  const handleClose = () => setShow(false)
  const handleShow = (id) => {
    setModalComplaintId(id)
    setShow(true)
  }

  const handleComplain = async (event) => {
    try {
      const form = event.currentTarget
      if (form.checkValidity() === false) {
        event.preventDefault()
        event.stopPropagation()
        setValidated(true)
      }
      if (
        !complaintImage ||
        !complaintTitle ||
        !complaintMessage ||
        !email ||
        !mobile ||
        !lastName ||
        !firstName
      ) {
        return message.error('Please Fill All The Fields')
      }
      event.preventDefault()
      event.stopPropagation()
      const data = {
        userId: user.uniqueId,
        complaintTitle: complaintTitle,
        complaintMessage: complaintMessage,
        mobile: mobile,
        firstName: firstName,
        lastName: lastName,
        email: email,
        screenshot: complaintImage,
      }
      const res = await axios.post('/api/v1/user/add-complaint', data)
      if (res.data.success) {
        setFirstname('')
        setLastname('')
        setMobile('')
        setEmail('')
        setComplaintMessage('')
        setComplaintTitle('')
        setComplaintImage('')
        message.success('Complaint Submitted Successfully')
        getComplaints()
      }
    } catch (error) {
      console.log(error)
      message.error('Something went wrong')
    }
  }
  const convertToBase64 = (file) => {
    if (file.size > 50 * 1024) {
      return message.error('File size should be less than 50kb')
    }
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.readAsDataURL(file)
      fileReader.onload = () => {
        resolve(fileReader.result)
        setComplaintImage(fileReader.result)
      }
      fileReader.onerror = (error) => {
        reject(error)
      }
    })
  }

  const getComplaints = async (page) => {
    try {
      const data = {
        userId: user.uniqueId,
      }
      const res = await axios.post(
        `/api/v1/user/get-complaints-paginated?page=${currentPage.current}&limit=${limit}`,
        data,
      )
      if (res.data.success) {
        const data = res.data.data
        setComplaintsHistory(data.results)
        setPageCount(data.totalPages) // Use total pages from the response
      } else {
        complaintMessage.error('Something went wrong')
      }

      console.log(res.data)
    } catch (error) {
      console.log(error)
    }
  }
  const handlePageClick = (e) => {
    const selectedPage = e.selected + 1
    currentPage.current = selectedPage
    getComplaints(selectedPage) // Fetch data for the selected page
  }

  useEffect(() => {
    getComplaints()
  }, [])

  return (
    <>
      <Container fluid className="d-flex flex-column-reverse flex-md-row col-12">
        <Container fluid className="col-12 col-md-7 mt-3 mt-md-0">
          <Card>
            <Card.Header>
              <div className="">Complaint Form</div>
            </Card.Header>
            <Card.Body>
              <Form noValidate validated={validated} onSubmit={handleComplain} autoComplete="off">
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Firts Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter First Name"
                      onChange={(e) => {
                        setFirstname(e.target.value)
                      }}
                      value={firstName}
                      required
                    />
                  </Form.Group>
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Last Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Last Name"
                      onChange={(e) => {
                        setLastname(e.target.value)
                      }}
                      value={lastName}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Unique Id</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Mobile Number"
                      value={user && user.uniqueId}
                      disabled
                    />
                  </Form.Group>

                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Email"
                      onChange={(e) => {
                        setEmail(e.target.value)
                      }}
                      value={email}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Mobile Number</Form.Label>
                    <Form.Control
                      type="number"
                      placeholder="Enter Mobile Number"
                      onChange={(e) => {
                        setMobile(e.target.value)
                      }}
                      value={mobile}
                      required
                    />
                  </Form.Group>
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>
                      Add a ScreenShot <span className="text-danger">*Note if neccessary</span>
                    </Form.Label>

                    <Form.Control
                      type="file"
                      placeholder="Enter Mobile Number"
                      onChange={(e) => {
                        convertToBase64(e.target.files[0])
                      }}
                      required
                    />
                  </Form.Group>
                </Row>
                <div className="mb-3 d-flex flex-column-reverse flex-md-row">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Complaint Title</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Complaint Title"
                      onChange={(e) => {
                        setComplaintTitle(e.target.value)
                      }}
                      value={complaintTitle}
                      required
                    />
                  </Form.Group>
                  <Container className="mb-3 mb-md-0">
                    <div className="d-flex">
                      {complaintImage && (
                        <>
                          <p className="me-3">Preview</p>
                          <div
                            className="d-flex"
                            style={{
                              background: '#041215',
                            }}
                          >
                            <img
                              src={complaintImage}
                              style={{
                                height: '100px',
                                width: '100px',
                                objectFit: 'contain',
                              }}
                              alt=""
                            />
                          </div>
                        </>
                      )}
                    </div>
                  </Container>
                </div>
                <Row className="mb-3">
                  <Form.Group as={Col} className="" controlId="formName">
                    <Form.Label>Complaint complaintMessage</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      placeholder="Enter Complaint complaintMessage"
                      onChange={(e) => {
                        setComplaintMessage(e.target.value)
                      }}
                      value={complaintMessage}
                      required
                    />
                  </Form.Group>
                </Row>
                <div className="d-flex justify-content-end">
                  <Button className="myCustomBtn" type="submit">
                    Submit
                  </Button>
                </div>
              </Form>
            </Card.Body>
          </Card>
        </Container>
        <Container fluid className="col-12 col-md-5">
          <Card className="col-12">
            <Card.Header>Contact Details</Card.Header>
            <Container className="d-flex flex-column align-items-center justify-content-between">
              <table className="m-3">
                <tr>
                  <td>
                    <i
                      style={{ background: '#4175fc' }}
                      className="fa-solid fa-phone p-2 rounded-5 text-white"
                    ></i>
                  </td>
                  <td>
                    <Link
                      style={{
                        textDecoration: 'none',
                        whiteSpace: 'nowrap',
                      }}
                      className="text-black ms-3"
                      to="tel:+91 - 9588285510"
                    >
                      +91 - 9588285510
                    </Link>
                  </td>
                </tr>
                <br />
                <tr>
                  <td>
                    <i
                      style={{ background: '#4175fc' }}
                      className="fa-solid fa-envelope p-2 rounded-5 text-white"
                    ></i>
                  </td>

                  <td>
                    <Link
                      style={{
                        textDecoration: 'none',
                      }}
                      className="text-black ms-3"
                      to="mailto:care.adinaths@gmail.com"
                      onClick={() => {
                        window.location.href = 'mailto:care.adinaths@gmail.com'
                      }}
                    >
                      care.adinaths@gmail.com
                    </Link>
                  </td>
                </tr>
                <br />
                <tr>
                  <td>
                    {' '}
                    <i
                      style={{ background: '#4175fc', whiteSpace: 'nowrap' }}
                      className="fa-solid fa-location-dot p-2 rounded-5 text-white"
                    ></i>
                  </td>
                  <td>
                    <p className="ms-3">Near ICICI Bank Varda Sagwara Dungarpur Rajasthan 314029</p>
                  </td>
                </tr>
              </table>
            </Container>
          </Card>
        </Container>
      </Container>
      <Container className="mt-3">
        <Container className="col-12">
          <Card>
            <Card.Body>
              <Table responsive>
                <thead>
                  <tr>
                    <td className="text-center">
                      <b>Sn</b>
                    </td>
                    <td className="text-center">
                      <b>Ticket No.</b>
                    </td>
                    <td className="text-center">
                      <b>Complaint Title</b>
                    </td>
                    <td className="w-50">
                      <b>Complaint complaintMessage</b>
                    </td>
                    <td className="text-center">
                      <b>Status</b>
                    </td>
                    <td className="text-center">
                      <b>Action</b>
                    </td>
                  </tr>
                </thead>
                <tbody>
                  {complaintsHistory && complaintsHistory.length > 0 ? (
                    complaintsHistory.map((item, index) => {
                      return (
                        <>
                          <tr key={index}>
                            <td className="text-center">{index + 1}</td>
                            <td className="text-center">{item.ticketId}</td>
                            <td className="text-center">{item.complaintTitle}</td>
                            <td className="w-50">{item.complaintMessage}</td>
                            <td className="text-center text-white">
                              {item.status === 'pending' ? (
                                <CBadge className="bg-warning">{item.status}</CBadge>
                              ) : (
                                <CBadge className="bg-success">{item.status}</CBadge>
                              )}
                            </td>
                            <td className="text-center">
                              <Button
                                onClick={() => {
                                  handleShow()
                                  handleShow(item._id)
                                  setModalTitle(item.complaintTitle)
                                  setModalMessage(item.complaintMessage)
                                  setModalScreenshot(item.screenshot)
                                }}
                                className="myCustomBtn"
                              >
                                View
                              </Button>
                            </td>
                          </tr>
                        </>
                      )
                    })
                  ) : (
                    <tr>
                      <td colSpan={6} className="text-center">
                        No Complaints Found
                      </td>
                    </tr>
                  )}
                </tbody>
              </Table>
            </Card.Body>
          </Card>
          <Modal show={show} onHide={handleClose} animation={false}>
            <Modal.Header closeButton>
              <Modal.Title>{modalTitle}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <li>{modalMessage}</li>
              <div
                className="mt-3 bg-black text-white"
                style={{
                  width: '100%',
                  height: '10rem',
                }}
              >
                {modalScreenshot ? (
                  <img
                    className="p-3"
                    style={{
                      width: '100%',
                      height: '100%',
                      objectFit: 'contain',
                    }}
                    src={modalScreenshot}
                    alt=""
                  />
                ) : (
                  'If your complaint is resolved then screenshot will be Deleted'
                )}
              </div>
            </Modal.Body>
            <Modal.Footer>
              <Button className="myCutomBtn" onClick={handleClose}>
                Close
              </Button>
            </Modal.Footer>
          </Modal>
          <Row className="mt-3">
            <ReactPaginate
              breakLabel="..."
              nextLabel="next >"
              onPageChange={handlePageClick}
              pageRangeDisplayed={5}
              pageCount={pageCount}
              previousLabel="< previous"
              renderOnZeroPageCount={null}
              marginPagesDisplayed={2}
              containerClassName="pagination justify-content-center"
              pageClassName="page-item"
              pageLinkClassName="page-link"
              previousClassName="page-item"
              previousLinkClassName="page-link"
              nextClassName="page-item"
              nextLinkClassName="page-link"
              activeClassName="active"
            />
          </Row>
        </Container>
      </Container>
    </>
  )
}
export default Index

